body {
  margin: 1em 2em;
  font-family: sans-serif;
}

.has-flex {
  display: flex;
}

.is-flexed {
  flex: 1 1;
  align-items: center;
}

.has-radius {
  border-radius: 3px;
}

.sr-only {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
}
header .version {
  padding: 2em 0;
  text-align: right;
}
.dice {
  text-align: center;
}

.dice input {
  width: 50%;
  text-align: center;
}

.dice .dice-options,
.dice .dice-result {
  margin-top: 1em;
}

.dice .dice-result  {
  font-size: 2em;
}
.nav {
  max-width: 15vw;
  min-width: 175px;
  margin-right: 2em;
}

.nav h2:first-of-type {
  margin-top: 0;
}

nav,
.nav .dice,
.nav .sheet-links {
  padding: 1em;
  border-radius: 3px;
  border: 1px solid #d2d2d2;
  background: whiteSmoke;
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav ul li button {
  display: block;
  width: 100%;
  text-align: left;
}

.nav .dice,
.nav .sheet-links {
  margin-top: 2em;
}
.github {
  padding: 1em;
}
.sheet {
  border: 1px solid #d2d2d2;
  border-radius: 3px;
  background: whiteSmoke;
  padding: 1em;
}

.sheet .has-flex {
  align-content: flex-start;
  border: 1px solid #d2d2d2;
}

.sheet .is-flexed {
  padding: .5em;
  border: 1px solid #d2d2d2;
}

.sheet header .controls {
  text-align: right;
}

.sheet header .controls button {
  margin: 1rem;
}

.sheet footer .controls {
  text-align: right;
}

.sheet footer .controls button {
  margin: 1rem;
}

.sheet section {
  margin-top: 3em;
}

.sheet label {
  display: block;
  margin-bottom: .25em;
  text-transform: capitalize;
  font-size: 80%;
}

.sheet input[type="number"] {
  width: 5em;
  text-align: center;
}

.sheet textarea {
  width: 100%;
  height: 5em;
}

.sheet .abbr {
  display: inline-block;
  border-radius: 3px;
  background: black;
  color: white;
  padding: .5em;
}

.sheet .is-fixed-width {
  flex: 0 1 auto;
  width: 10em;
}

.sheet input,
.sheet select,
.sheet textarea {
  padding: .25em;
  border-radius: 3px;
  border: 1px solid #d2d2d2;
  background: white;
}

.sheet .is-important input,
.sheet .is-important select,
.sheet .is-important textarea {
  border-radius: 3px;
  font-weight: bold;
  border-color: cornflowerblue;
  box-shadow: 0 0 0 3px lightskyblue;
}

.sheet .is-key input,
.sheet .is-key select,
.sheet .is-key textarea {
  border-radius: 3px;
  font-weight: bold;
  border-color: goldenrod;
  box-shadow: 0 0 0 3px rgb(221, 221, 108);
}

.sheet #skills .is-flexed span {
  display: inline-block;
  font-weight: bold;
  padding: .5em;
}

.sheet #skills input[type="checkbox"] {
  text-align: center;
  margin-top: 1em;
  height: 1.5em;
  width: 1.5em;
  font-size: 1.5em;
}
.app-main .is-not-flexed {
  display: inline-block;
  width: 33.3%;
  padding: 0 1em 1em 1em;
}

.player-card {
  background: white;
  border: 1px solid #d2d2e2;
  border-radius: 3px;
  padding: 1em;
  height: 15em;
}

.player-card dl dt,
.player-card dl dd {
  display: block;
  margin: 0;
}

.player-card dl dt {
  font-weight: bold;
}

.player-card dl dd {
  margin-bottom: .5em;
}
